// main: main.scss

.sign_status {
  font-size:$default-font-size;
  color:$color-text;
}

.sign_sns {
  display:inline-block;
  width:48px;
  height:45px;
  line-height:45px;
  color:$color-white;
  border-radius:$default-border-radius;
  font-size:($title-font-size + 4);


  &.sign_facebook {
    margin-right:10px;
    background-color:#36549d;
  }

  &.sign_google {
    background-color:#c64c38;
  }
}

.txt_or {
  margin-top:5px;
  overflow:hidden;
  font-family:$semibold-font;
  font-size:($default-font-size - 3);

  &:before, &:after {
    background-color: $border-color;
    content: "";
    display: inline-block;
    height: 1px;
    position: relative;
    vertical-align: middle;
    width: 50%;
  }

  &:before {
    right: .5em;
    margin-left: -50%;
  }

  &:after {
    left: .5em;
    margin-right: -50%;
  }
}

.input_wrap {
  position:relative;
  margin:12px 0;

  > i {
    position:absolute;
    top:9px;
    right:10px;
    color:$input-border-color;
  }
}

.input_sign {
  padding-right:33px;
  font-size:($default-font-size + 1);
}

.check_wrap {
  margin:16px 0;
  text-align:left;
  > li {
    float:left;
    width:50%;
    margin-bottom:15px;

    > .check_label {
      color:#3c454c;
      font-family:$semibold-font;
      font-size:$default-font-size;
    }
  }
}

.check_input {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
}

.check_label {
  display: inline-block;
  font-size:($default-font-size + 1);
  color:#a3a7aa;
  cursor: pointer;
  margin-right:15px;

  &:before {
    content: "";
    display: inline-block;
    width: 21px;
    height: 21px;
    box-shadow: inset 0 0 0 2px $input-border-color;
    border-radius:$default-border-radius;
    -webkit-font-smoothing: antialiased;
    line-height: 18px;
    margin-right:4px;
    float: left;
    font-weight:600;
  }

  &:last-child {
    margin-right:0;
  }

  > span {
    color:#a3a7aa;
    font-family:$default-font;
  }
}

input[type=checkbox].check_input:checked + label.check_label:before {
    font-family: 'feather';
    content: "\e116";
    box-shadow: none;
    background-color: #00c6e7;
    color: #fff;
    font-size: 16px;
    text-align:center;
}

.txt_forgot {
  display:inline-block;
  margin:15px 0;
  color:$default-color-dark;
  font-size:($default-font-size + 2);
}

.txt_signup {
  margin-top:15px;
  padding-top:15px;
  color:$color-text;
  font-size:($default-font-size + 2);
  border-top:1px solid $border-color;

  > a {
    padding-left:6px;
    color:$default-color-dark;
  }
}

.txt_agree {
  margin-top:15px;
  color:#3c454c;
  font-size:($default-font-size - 1);
  font-family:$default-font;

  > a {
    color:$default-color-dark;
  }
}

.notice_signup {
  margin-top:5px;
  color:#a3a7aa;
  font-size:($default-font-size - 2);
  line-height:14px;

  &.txt_valid {
    margin-top:15px;
    color:$default-color-dark;
  }
}

.txt_choose {
  color:#a3a7aa;
  font-size:($default-font-size - 1);
}
