// main: main.scss

.mypage_top {
  padding:37px 17px;
  background:$default-color-dark url('../../assets/images/bg_mypage.png') no-repeat 0 0;
  background-size:cover;
  text-align:center;
}

.img_profile {
  width:140px;
  height:140px;
  border-radius:50%;
}

.profile_name {
  display:block;
  margin-top:17px;
  font-size:($title-font-size + 2);
  font-family:$bold-font;
  color:$color-white;
  word-break:break-word;
}

.profile_email {
  font-size:($default-font-size - 1);
  color:$color-white;
}

.mypage_menu {
  border-bottom:1px solid $border-color;
  margin-bottom:0;
}

.status_writing {
  margin-bottom:20px;
  font-size:($default-font-size - 1);
  color:$color-text;
}

.box_password {
  width:240px;
  margin:0 auto;

  input {
    margin-bottom:10px;
    font-size:($default-font-size + 1);
  }
}

.change_profile {
  position:relative;
  display:inline-block;

  .active_profile {
    display:none;
    position:absolute;
    top:0;
    left:0;
    width:140px;
    height:140px;

    > .active_txt {
      display:block;
      position:absolute;
      top:26px;
      left:40px;
      z-index:2;
      color:#f7fafb;
      font-size:($default-font-size - 3);
      > i {
        display:block;
        margin-bottom:8px;
        font-size:53px;
      }
    }

    > .active_layer {
      display:block;
      position:absolute;
      top:0;
      left:0;
      width:140px;
      height:140px;
      border-radius:50%;
      background-color:#000;
      opacity:0.5;
    }
  }

}

.btn_trash {
  color:$color-topic;
  font-size:($default-font-size - 1);
  line-height:20px;
}
