// main: main.scss

.visual_top {
  width:100%;
  height:200px;
  padding:52px 17px;
  background:url('../../assets/images/bg_cheminfo.jpg') no-repeat 0 0;
  background-size:cover;
  text-align:center;

  &.visual_module {
    padding-top:66px;
    background-image:url('../../assets/images/bg_module.jpg');
  }

  > h1 {
    display:inline-block;
    color:$default-color;
    font-size:($title-font-size + 4);
    font-family:$semibold-font;
    border-bottom:1px solid $default-color;
    line-height:22px;
  }

  > p {
    margin-top:10px;
    color:$color-white;
    font-size:($title-font-size - 2);
    font-family:$bold-font;
    line-height:22px;
  }
}

.comming_soon {
  padding:44px 17px 38px;
  background-color:$default-bg;
  text-align:center;

  > h2 {
    font-family:$semibold-font;
    font-size:($title-font-size - 2);
    color:$color-text;
    text-transform:uppercase;
  }

  > p {
    margin:8px 0 17px;
    font-size:($default-font-size - 1);
    color:#989b9e;
    > a {
      color:#989b9e;
      border-bottom:1px solid #989b9e;
    }
  }
}

.btn_goto_pc2 {
  display:inline-block;
  padding:7px 37px 9px;
  color:$color-white;
  font-size:($default-font-size + 2);
  border-radius:$default-border-radius;
  background-color:#3c454c;
}

.summary_cheminfo {
  margin:44px 17px 20px;
  text-align:center;

  > li {
    margin-bottom:30px;

    > span {
      display:inline-block;
      width:85px;
      height:85px;
      overflow:hidden;
      background:url('../../assets/images/icon_cheminfo.png') no-repeat 0 0;
      background-size:85px 255px;

      &.thumb2 {
        background-position:0 -85px;
      }

      &.thumb3 {
        background-position:0 -170px;
      }

      &.thumb4 {
        width:99px;
        height:95px;
        background:url('../../assets/images/icon_module.png') no-repeat 0 0;
        background-size:99px 188px;
      }

      &.thumb5 {
        width:99px;
        height:95px;
        background:url('../../assets/images/icon_module.png') no-repeat 0 0;
        background-size:99px 188px;
        background-position:0 -101px;
      }
    }
  }

  h3 {
    margin:5px 0 3px;
    color:$default-color;
    font-size:($title-font-size - 2);
    font-family:$semibold-font;
  }

  p {
    font-size:($default-font-size + 1);
    color:$color-text;
  }
}
