// main: main.scss

// component
.forum_menu {
  width:100%;
  margin-bottom:5px;
  padding:5px 0 0 15px;
  background-color:$color-white;
  overflow:hidden;

  > ul {
    height:40px;
    white-space:nowrap;
    overflow-x:auto;

    > li {
      display:inline-block;
      margin:5px 10px 0 0;

      > a {
        display:block;
        padding:4px 4px 9px;
        color:#a6afb6;
        font-size:($default-font-size + 2);
        &.active {
          border-bottom:2px solid $color-text;
          color:$color-text;
        }
      }
    }
  }
}

.alink-write {
  display:block;
  width:100%;
  margin:5px 0;
  padding:15px 17px;
  background-color:$color-white;
  box-sizing:border-box;
  color:$default-color;
  font-size:($default-font-size + 2);

  &.alink-opinion {
    margin:-5px 0 0 0;
    background-color:$default-color;
    color:$color-white;
  }
}

.icon_write {
  float:right;
  width:36px;
  height:36px;
  padding-top:7px;
  border-radius:50%;
  background-color:#f6f6f6;
  text-align:center;
  vertical-align:middle;
  box-sizing:border-box;
  > i {
    font-size:18px;
  }

  &.icon_opinion {
    background-color:$color-white;
    color:$default-color;
  }
}

.forum_article {
  margin-bottom:5px;
  padding-top:20px;
  background-color:$color-white;

  &:last-child {
    margin-bottom:0;
  }
}

.link_article {
  display:block;
  > .inner_padding {
    padding:18px 17px 20px;

    > h1 {
      font-size:($default-font-size + 5);
      color:$color-text;
      font-family:$semibold-font;
    }
  }
}

.info_profile {
  float:left;
  width:50px;
}

.info_writer {
  float:left;
  width:80%;
  line-height:16px;
}

.writer_name {
  display:block;
  color:$default-color;
  font-size:($default-font-size - 1);
  @extend %ellipsis;
}

.write_date, .write_state {
  color:#a6afb6;
  font-size:($default-font-size - 3);
}

.write_case {
  display:inline-block;
  height:13px;
  line-height:10px;
  > .icon-globe {
    color:#a6afb6;
    font-size:($default-font-size - 1);
  }
}


.divide_dot {
  display:inline-block;
  width:2px;
  height:2px;
  border-radius:50%;
  background-color:#a6afb6;
  vertical-align:middle;
}

.article_img {
  display:block;
  width:100%;
  //min-height:160px;
}

.icon_set {
  margin-top:20px;
  padding-bottom:16px;
  > ul {
    float:left;

    > li {
      position:relative;
      float:left;
      margin-right:18px;
      color:#aab8c2;
      font-size:($default-font-size - 1);
    }
  }

  a {
    display:inline-block;
    color:#aab8c2;
    > i {
      display:inline-block;
      font-size:($default-font-size + 3);
      vertical-align:middle;
      margin-top:1px;

      &.fa-heart-o, &.fa-heart, &.fa-pencil {
        font-size:($default-font-size + 2);
      }

      &.fa-bookmark-o, &.fa-bookmark {
        margin-top:4px;
      }
    }
    > span {
      display:inline-block;
      vertical-align:middle;
    }
  }
  .active, a:active {
    color:$default-color;
  }

  > .btn_opinion {
    float:right;
  }

  &:after {
    @extend %clear;
  }
}

.layer_share {
  display:none;
  position:absolute;
  bottom:20px;
  left:-48px;
  z-index:2;
  padding:10px 15px 8px;
  background-color:$color-white;
  border-radius:4px;
  box-shadow: 2px 1px 5px #e5e5e5;
  border:1px solid #ddd;
  font-size:($default-font-size - 1);
  color:$color-text;
  white-space:nowrap;

  &.layer_edit {
    right:0;
    left:initial;

    .txt_edit {
      display:block;
      padding:0 4px 6px;
      color:$color-text;
      &:nth-child(2) {
        padding:6px 4px 3px;
        border-top:1px solid $border-color;
      }
    }
  }

  > h2 {
    margin-bottom:10px;
    font-weight:normal;
  }

  .share_facebook, .share_google {
    display:inline-block;
    width:45px;
    height:45px;
    margin-bottom:3px;
    border-radius:50%;
    color:$color-white;
    text-align:center;
    line-height:40px;
    > i {
      font-size:20px;
    }
  }

  .share_facebook {
    margin-right:10px;
    background-color:#36549d;
  }

  .share_google {
    background-color:#c64c38;
  }
}

.banner_box {
  display:block;
  padding:10px 17px 15px;
  height:182px;
  text-align:center;
  box-sizing:border-box;
  overflow:hidden;

  .main_banner1 {
    display:block;
    height:100%;
    background:url('../../assets/images/main_banner1.jpg') no-repeat 0 0;
    background-size:cover;
    border-radius:3px;
  }

  h3 {
    padding:55px 0 10px;
    color:$color-white;
    font-size:($default-font-size + 2);
    font-family:$semibold-font;
  }

  p {
    color:#bad1e7;
    font-size:($default-font-size - 1);
    font-family:$semibold-font;
  }

  &.banner_box2 {
    height:120px;

    .main_banner2 {
      background-image:url('../../assets/images/proleaders_banner1.jpg');
    }

    h3 {
      padding:20px 0 3px;
    }
  }
}

.report_drop {
  position:relative;
  float:right;

  .icon_report {
    display:block;
    font-size:($title-font-size + 2);
    margin-top:-8px;
    padding-left:5px;
  }
}

.file_attach {
  display:inline-block;
  background-color:#f0f3f3;
  padding:6px 7px 5px;
  font-size:($default-font-size - 1);
  font-family:$semibold-font;
  color:$color-text;
  word-break:break-all;
  text-align:left;

  > img {
    width:17px;
    height:22px;
    vertical-align:middle;
    margin:-1px 3px 0 0;
  }
}

.area_opinion {
  background-color:$color-white;
}

.tit_opinion {
  padding:15px 17px;
  color:$color-text;
  font-family:$semibold-font;
  font-size:($default-font-size - 1);

  > span {
    color:$default-color;
  }
}

.list_opinion {
  overflow:hidden;
  > li {
    border-top:1px solid $border-color;
    margin:0 17px;
    padding-top:20px;
  }
}

.txt_opinion {
  height:54px;
  overflow:hidden;

  &.txt_opinion_all {
    height:auto;
  }
}

.txt_seemore {
  color:#95a1aa;
  font-size:($default-font-size - 2);
}

.list_comment {
  display:none;
  > li {
    margin-bottom:10px;
    padding:15px 15px 0;
    background-color:#f8f8f8;
  }
}

.icon_download {
  display:block;
  width:40px;
  margin:10px auto;
  color:$default-color-dark;
  font-size:($title-font-size + 12);
}

.list_file_information {
  border-top:1px solid $border-color;

  > li {
    padding:16px 22px 12px;
    border-bottom:1px solid $border-color;

    > h2 {
      color:$default-color-dark;
      font-size:$default-font-size;
      font-weight:normal;
    }

    > p {
      color:$color-text;
      font-size:($default-font-size - 2);
    }

    &:last-child {
      border-bottom:0;
    }
  }
}


// write topic
.topic_type {
  padding:10px 17px;
  border-top:1px solid $border-color;
  border-bottom:1px solid $border-color;
  font-size:($default-font-size - 1);

  > a {
    display:block;
    color:$color-topic;
    > .fa {
      float:right;
    }
  }
}

.timeline_type {
  padding:4px 17px 3px 12px;
  border-bottom:1px solid $border-color;
}

.layer_topic_type {
  z-index:6;
}

.acc_topic {
  border-top:1px solid $border-color;
  > li {
    padding:10px 32px 10px 30px;
    border-bottom:1px solid $border-color;
    font-size:($default-font-size);
    color:$color-topic;
    vertical-align:middle;
    > .fa {
      float:right;
      margin-top:5px;
      font-size:($title-font-size + 2);
      color:$color-arrow;
    }

    > p {
      display:none;
      font-size:($default-font-size - 2);
      margin-left:34px;
    }
  }
}

.area_write {
  padding:18px 17px;

  > .info_writer {
    margin:8px 0 20px;
  }
}

.input_img {
  display:inline-block;
  position:relative;
  vertical-align:top;
  overflow:hidden;

  input[type="file"] {
    position: absolute;
    top: 4px;
    left: 0;
    width: 80%;
    height: 22px;
    opacity: 0;
  }
}

.input_file {
  position:fixed;
  bottom:0;
  width:100%;
  padding:15px 10px;
  background-color:$default-bg;
  font-size:$title-font-size;

  .input_img {
    margin:0 40px 0 10px;
  }

  .alink-file {
    padding:0;
  }

  .fa-paperclip {
    display:inline-block;
    -ms-transform: rotate(-90deg); /* IE 9 */
    -webkit-transform: rotate(-90deg); /* Chrome, Safari, Opera */
    transform: rotate(-90deg);
    vertical-align:bottom;
    font-size:($title-font-size + 2);
  }
}

.view_attach {
  position:relative;
  display:inline-block;

  > .file_attach {
    padding-right:30px;
  }

  > button {
    position:absolute;
    top:0;
    right:0;
    width:25px;
    height:25px;
    margin:0;
    padding:0;
    border-radius:$default-border-radius;
    background-color:$color-text;
    color:$color-white;
    overflow:hidden;
    box-sizing:border-box;

    > i {
      display:block;
      margin:-9px;
    }
  }
}

.layer_file_attach {
  h2 {
    margin:15px 0 3px;
    color:$default-color-dark;
    font-weight:normal;
    font-size:$default-font-size;
  }
  .select_box {
    float:left;
    margin-right:1%;

    > select {
      font-size:($default-font-size + 1);
    }

    &.select_month {
      width:44%;
    }

    &.select_day {
      width:24%;
    }

    &.select_year {
      //float:right;
      width:30%;
      margin-right:0;
    }
  }
}
