// main: main.scss

.visual_about {
  width:100%;
  height:246px;
  padding-top:110px;
  background:url('../../assets/images/bg_about.jpg') no-repeat 0 0;
  background-size:cover;
  text-align:center;

  > h1 {
    display:inline-block;
    color:$color-white;
    font-size:($title-font-size - 2);
    font-family:$bold-font;
  }
}

.summary_about {
  margin:44px 17px 20px;
  color:$color-text;
  text-align:center;

  > li {
    margin-bottom:38px;

    > span {
      display:inline-block;
      width:71px;
      height:52px;
      overflow:hidden;
      background:url('../../assets/images/icon_about.png') no-repeat 0 0;
      background-size:71px 330px;

      &.about2 {
        height:66px;
        background-position:0 -60px;
      }

      &.about3 {
        height:63px;
        background-position:0 -135px;
      }

      &.about4 {
        height:55px;
        background-position:0 -208px;
      }

      &.about5 {
        height:55px;
        background-position:0 -275px;
      }
    }
  }

  h2 {
    margin:5px 0;
    font-size:($title-font-size - 2);
    font-family:$semibold-font;
    line-height:20px;
  }

  p {
    font-size:($default-font-size + 1);
    line-height:18px;
  }
}
