// main: main.scss

// help component
.hidden {
  position:fixed;
  overflow:hidden;
}

.txt_hidden {
  display:block;
  width:1px;
  height:1px;
  font-size:1px;
  line-height:3px;
  color:transparent;
  overflow:hidden;
}
%clear {
  clear:both;
  display:block;
  content:'';
}
%ellipsis {
  text-overflow:ellipsis;
  white-space:nowrap;
  //word-wrap:normal;
  overflow:hidden;
}

.btn {
  border-radius:$default-border-radius;
  padding:6px 10px;
  color:$color-white;
  font-size:($default-font-size - 3);
  font-family:$semibold-font;
  background-color:#aab8c2;
}

.btn_default {
  padding:6px 25px;
  font-size:($default-font-size);
  background-color:$default-color;
}

.btn_wide {
  width:100%;
  padding:8px 25px;
  font-size:($title-font-size - 1);
}

.btn_back {
  margin-right:5px;
  padding:6px 25px;
  font-size:($default-font-size);
  background-color:#d3dae0;
  color:$color-text;
}

.icon_back {
  position:absolute;
  top:17px;
  left:11px;
  font-size:($title-font-size - 2);
}

.btn_post {
  position:absolute;
  top:17px;
  right:11px;
  font-size:($default-font-size + 2);
  text-transform:uppercase;
  color:$default-color;
  font-family:$semibold-font;
}

.btn_round {
  color:$default-color;
  border:1px solid $default-color;
  background-color:$color-white;
  font-size:$default-font-size;
  box-sizing:border-box;
}

.btn_save {
  padding:7px 15px;
}

.wrap_align_center {
  margin:12px 0 34px;
  text-align:center;
}


// common component
.icon_profile {
  width:36px;
  height:36px;
  border-radius:50%;
  vertical-align:middle;
  margin-right:9px;
}

.inner_padding {
  padding:0 17px;
}

// layout
.wrap {
  position:relative;
  background-color:$color-white;
  height:100%;
}

.gnb {
  position:fixed;
  width:100%;
  height:46px;
  background-color:$default-color;
  color:$color-white;
  vertical-align:middle;
  z-index:3;

  > .gnb_menu {
    float:left;
    width:48px;
    height:46px;
    background:none;
    border:0;

    &.active {
      background-color:#009cb6;
      color:$color-white;
    }
  }

  > .home, > .signin {
    display:inline-block;
    height:inherit;
    padding:12px 4px 0 4px;
    color:inherit;
    box-sizing: border-box;

    &:visited, &:link {
      color:inherit;
      text-decoration:none;
    }
  }

  > .home {
    text-transform: uppercase;
    font-family:$semibold-font;
    font-size:($default-font-size + 2);
  }

  > .signin {
    float:right;
    font-size:($default-font-size + 1);
  }

  > .total_search {
    float:right;
    width:48px;
    height:46px;
    background:none;
    border:0;
    color:inherit;
    text-align:center;
    line-height:44px;
  }

  .gnb_profile {
    float:right;
    margin:9px 17px 0 0;
    width:30px;
    height:30px;
    border-radius:50%;
  }

  &.gnb_type1 {
    background-color:$color-white;
    color:$color-text;
    border-bottom:1px solid $border-color;
  }

  &.gnb_type2 {
    background-color:transparent;
  }

  &.gnb_type3 {
    background-color:#3c454c;
  }
}

.nav-up {
  position:relative;
}

.gnb_menu_list {
  display:none;
  width:100%;
  position:absolute;
  top:45px;
  padding-top:10px;
  background-color:$color-white;
  border-top:1px solid $border-color;
  z-index:2;

  > ul > li {
    padding:8px 16px;

    &:last-child {
      margin-top:10px;
      padding:21px 16px;
      border-top:1px solid $border-color;
      border-bottom:1px solid $border-color;
    }

     i {
      float:right;
      display:block;
      width:13px;
      height:13px;
      margin-top:4px;
      font-size:13px;
      color:#4c5760;

      &.fa-angle-down {
        font-size:20px;
      }
    }

    &:after {
      @extend %clear;
    }
  }

  > ul > li > a {
    display:block;
    width:100%;
    padding:8px;
    color:$color-text;
    font-size:($default-font-size + 2);
    box-sizing:border-box;

    &.forum-child {
      float:left;
      width:80%;
    }
  }

  .gnb_forum {
    float:right;
    padding-bottom:10px;
    overflow:hidden;
  }
}


.gnb_arrow {
  position:absolute;
  top:-8px;
  width: 16px;
  height: 8px;
  margin-left:17px;
  //border:6px solid $color-white;
  //border-top-width: 0;
  //border-right-color: transparent;
  //border-left-color: transparent;

  &.right_arrow {
    right:0;
    margin-right:23px;
  }
  > svg {
    vertical-align:top;
  }
}

.forum_sub {
  display:none;
  clear:both;
  width:100%;
  > li {
    padding:4px 15px;

    &:last-child {
      padding-bottom:0;
    }

    > a {
      display:block;
      width:100%;
      padding:4px;
      color:#6b747c;
      font-size:($default-font-size + 1);
      box-sizing: border-box;
    }
  }
}

.goto_pc {
  padding:35px;
  background:url('../../assets/images/bg_gopc.png') no-repeat 0 0;
  background-size:cover;
  text-align:center;

  > h1 {
    color:$color-white;
    text-transform:uppercase;
    font-size:($default-font-size + 2);
    font-family:$bold-font;
  }
  > p {
    font-size:($default-font-size - 2);
    color:$color-less-white;
  }

  .btn_goto_pc {
    display:inline-block;
    margin-top:10px;
    padding:5px 17px;
    border:2px solid $default-color;
    border-radius: $default-border-radius;
    color:$color-white;
    font-family:$semibold-font;
    font-size:($default-font-size + 2);
  }
}

.footer {
  padding:24px 15px 34px;
  background-color:#272d32;

  > address {
    color:#cfd0d0;
    font-size:($default-font-size - 3);
    font-style:normal;
  }
}

.link_sns {
  margin-bottom:21px;

  > li {
    float:left;
    margin-right:13px;

    > a {
      display:block;
      width:44px;
      height:44px;
      border-radius:50%;
      background-color:#3a4148;
      line-height:46px;
      text-align:center;

      > i {
        font-size:20px;
        color:$color-white;
      }
    }

    &:last-child {
      margin-right:0;
    }
  }

  &:after {
    @extend %clear;
  }
}

.terms {
  > li {
    float:left;
    margin-right:13px;

    > a {
      font-size:12px;
      color:#dfe0e0;
    }

    &:last-child {
      margin-right:0;
    }
  }

  &:after {
    @extend %clear;
  }
}

.btn_top {
  display:none;
  position:fixed;
  right:17px;
  bottom:10px;
  width:30px;
  height:30px;
  padding:0;
  background-color:$default-color;
  border:0;
  border-radius:$default-border-radius;
  text-align:center;
  z-index:2;

  > i {
    font-size:24px;
    color:$color-white;
  }
}

.page_ws {
  height:40px;
  margin:0;
  padding:0;
  border:0;
  background-color:transparent;
}

.layer_wrap {
  display:none;
  position:fixed;
  width:100%;
  height:100%;
  top:0;
  left:0;
  background-color:$color-white;
  z-index:5;
}

.layer_header {
  text-align:center;
  padding:18px 0;
}

.layer_body {
  text-align:center;
  padding:0 17px;
}

.tit_layer {
  margin-bottom:16px;
  font-size:($default-font-size + 2);
  color:$color-text;
  font-weight:normal;
  font-family:$semibold-font;

  &.tit_sign {
    font-size:$title-font-size;
    text-transform:uppercase;
  }
}

.layer_close, .sign_close {
  position:absolute;
  top:10px;
  right:11px;
  width:34px;
  text-align:center;
  font-size:($title-font-size + 4);
  outline:none;
  color:$color-text;
}

.txt_content {
  margin-top:11px;
  font-size:$default-font-size;
  color:$color-text;

  &.txt_content_view {
    font-size:($default-font-size + 1);
    margin-bottom:14px;
  }
}

.txt_explain {
  margin-bottom:20px;
  font-size:($default-font-size - 1);
  color:$color-text;
}

.txt_notice {
  font-size:($default-font-size - 2);
  color:$color-text;
  > a {
    color:$default-color;
  }

  &.txt_file_upload {
    color:#a3a7aa;
  }
}


// form
.search_input {
  //margin-bottom:5px;
  padding:17px 17px 20px;
  background-color:$color-white;
}

.input_base {
  width:100%;
  padding:8px 12px;
  border:1px solid $input-border-color;
  border-radius:$default-border-radius;
  box-sizing: border-box;
  color:$color-topic;
  font-size:$default-font-size;
}

input[type="radio"].css-radio{
  visibility:hidden;
  width:1px;
  height:1px;
  overflow:hidden;
}
.css-radio-label{
  font-size:$default-font-size;
  color:$color-text;
  line-height:30px;
  margin-right:10px;
}
input[type=radio].css-radio + .css-radio-label::before{
  background-clip:content-box;
  border:2px solid #a8a8a8;
  border-radius:50%;
  content:"";
  display:inline-block;
  width:9px;
  height:9px;
  margin:7px 12px 0 0;
  padding:2px;
  vertical-align:top;
}
input[type="radio"].css-radio:checked + .css-radio-label::before{ background-color:$default-color; border-color:$default-color;}

.input_type1 {
  padding-right:0;
  padding-left:0;
  border:0;
  border-bottom:2px solid $input-border-color;
  font-size:$title-font-size;
}

.textarea_type1 {
  min-height:120px;
  padding-right:0;
  padding-left:0;
  border:0;
  resize:none;
  overflow-y:auto;
  font-size:($default-font-size + 1);
}



.select_box {
  position:relative;

  label {
    //position: absolute;
    //top:0;
    //left:0;
    display: block;
    text-overflow: ellipsis;
    overflow: hidden!important;
    white-space: nowrap;
    height:38px;
    //z-index:3;
  }

  > select {
    font-size:($default-font-size);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height:38px;
    outline: 0;
    -webkit-border-radius: inherit;
    border-radius: inherit;
    -webkit-appearance: none;
    -moz-appearance: none;
    cursor: pointer;
    filter: Alpha(Opacity=0);
    opacity: 0;
    z-index: 2;
  }
  > i.fa-angle-down {
    position:absolute;
    top:11px;
    right:3px;
    width:24px;
    font-size:18px;
    background-color:#fff;
  }
}

.txt_area {
  height:140px;
  font-size:($default-font-size);
  resize:none;
}

.no_result {
  //margin-top:-5px;
  background-color:$color-white;
  text-align:center;
  padding:50px 0;
  font-size:($default-font-size - 1);
  color:$color-text;

  &.no_result_bookmark {
    height:80%;
    //margin-top:-30px;
    //background-color:$default-bg;
  }
}

.fileinput {
  position:relative;
  padding-left:3px;
  height:36px;

  input[type="file"], .btn {
    position: absolute;
    top: 3px;
    left: 3px;
    width: 82px;
    height: 28px;
    font-size:($default-font-size - 2);
    font-family:$default-font;
    box-sizing:border-box;
  }

  input[type="file"] {
    opacity: 0;
  }

  .fileinput-filename {
    display:inline-block;
    width:65%;
    padding-left:92px;
    @extend %ellipsis;
  }
}

.modal {
  position:fixed;
  top:0;
  left:0;
  width:100%;
  height:100%;
  background-color:transparent;

  .bg_layer {
    width:100%;
    height:100%;
    background-color:#000;
    opacity:0.4;
  }
}

.modal_wrap {
  position:absolute;
  top:24%;
  left:50%;
  margin-left:-140px;
  width:280px;
  background-color:$color-white;
  border-radius:3px;
  text-align:center;
  z-index:2;

  .modal_head {
    margin-bottom:22px;
    padding:18px 0 22px;
    border-bottom:1px solid $border-color;
    font-size:($default-font-size + 2);
    font-family:$semibold-font;

    &.modal_head2 {
      padding:38px 0 0 0;
      border:0;

      > h1 {
        padding-bottom:5px;
      }
    }
  }

  .modal_body {
    padding:0 17px;
  }
}
