// main: main.scss

.wrap_terms {
  padding:10px 17px;
  color:#73787c;
  font-size:($default-font-size - 1);

  h1, h2, h3 {
    font-family:$semibold-font;
    color:$color-text;
  }

  h1 {
    margin:10px 0;
    font-size:($title-font-size + 6);
  }

  h2 {
    margin:8px 0;
    font-size:($title-font-size - 1);
  }

  h3 {
    margin:5px 0;
    font-size:$default-font-size;
  }

  strong {
    font-family:$semibold-font;
  }

  em {
    font-style:normal;
  }

  a {
    color:$default-color-dark;
    word-wrap:break-word;
  }

  ol {
    margin:10px 0 10px 20px;
    li {
      list-style:decimal;
      margin-bottom:7px;
    }
  }

  ul {
    margin:10px 0 10px 20px;
    li {
      list-style:disc;
      margin-bottom:7px;
    }
  }


  .terms-paragraph {
    margin:10px 0;
    padding-bottom:10px;
    border-bottom:1px solid $border-color;
  }

  .text-addition {
    padding:10px 0;
  }

  .inner-paragraph {
    margin-bottom:15px;
  }
}
