// main: main.scss

.proleaders_list {
  margin-bottom:5px;
  padding:25px 17px;
  background-color:$color-white;
  overflow:hidden;

  &:last-child {
    margin-bottom:0;
  }
}

.proleaders {
  .writer_name {
    font-size:($default-font-size + 3);
    color:$color-text;
    font-family:$bold-font;
  }
}

.fa-graduation-cap {
  color:$default-color;
  font-size:$default-font-size;
}

.icon_proleaders {
  float:left;
  position:relative;
  margin-right:15px;
  > .fa {
    position:absolute;
    bottom:0;
    right:0;
    width:20px;
    height:20px;
    line-height:20px;
    color:#959a9e;
    background-color:$color-white;
    font-size:($default-font-size - 3);
    text-align:center;
    border-radius:46%;
  }
  > img {
    width:51px;
    height:51px;
    border-radius:50%;
  }
}

.info_proleaders {
  float:left;
  width:75%;
  > .fa {
    float:left;
    margin:5px 3px 0 0;
  }

  > h1 {
      margin-top:2px;
      height:37px;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      word-wrap:break-word;
      color:$color-text;
      font-size:$default-font-size;
      font-weight:400;
  }

  > .txt_content {
    margin-top:2px;
    height:29px;
    line-height:14px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    word-wrap:break-word;
    color:#96a2b1;
    font-size:($default-font-size - 2);
  }
}

.proleaders_view {
  padding:27px 17px 20px;
  background-color:$color-white;

  > .icon_proleaders {
    float:none;
    width:78px;
    height:78px;
    margin:0 auto;
    > img {
      width:78px;
      height:78px;
    }

    > .fa {
      width:26px;
      height:26px;
      line-height:25px;
      font-size:$default-font-size;
    }
  }

  h1 {
    color:$color-text;
    font-size:($default-font-size + 1);
    font-weight:400;
    text-align:center;
  }
}

.pro_name {
  margin:20px 0 5px;
  text-align:center;

  .fa-graduation-cap {
    font-size:($default-font-size + 2);
  }

  .writer_name {
    display:inline;
    white-space:normal;
    font-size:($title-font-size + 1);
    line-height:19px;
  }
}

.link_proleaders {
  margin-top:10px;
  text-align:center;
  a {
    display:inline-block;
    color:$default-color;
    font-size:($default-font-size - 3);
    word-break:break-all;
    line-height:12px;
    vertical-align:top;
  }
}

.pro_item {
  position:relative;
  margin-top:5px;
  padding:25px 17px 20px;
  background-color:$color-white;
  overflow:hidden;

  > h2 {
    color:$default-color-dark;
    font-size:($default-font-size + 2);
    font-family:$semibold-font;
  }

  > .item_paragraph {
    margin-top:3px;
    font-size:($default-font-size - 1);
    color:$color-text;
    line-height:17px;
  }

  > .layer_paid {
    display:none;
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    margin-top:50px;
    background:url('../../assets/images/bg_payment.png') repeat-x 0 0;
    background-size:contain;
  }

  > .txt_paid {
    display:none;
    position:absolute;
    top:76px;
    left:0;
    width:100%;
    font-size:($default-font-size - 2);
    color:$color-text;
    text-align:center;
    z-index:2;
  }

  &.paid_item {
    height:130px;

    .txt_paid, .layer_paid {
      display:block;
    }
  }

}
